import {
	PAGE_ANSWER_QUESTION
} from '../../static/page-path.js'
export default {
	data() {
		return {
			autoplay: false,
			interval: 2000,
			duration: 500,
			current:0
		}
	},
	methods: {
		_goToAnswerQuestion() {
			uni.navigateTo({
				url: PAGE_ANSWER_QUESTION
			})
		},
		_clickright() {
			if (this.$data.current < 2) {
				this.$data.current = this.$data.current + 1;
			} else {
				this.$data.current = 0;
			}

		},

		_clickleft() {
			if (this.$data.current == 0) {
				this.$data.current = 2;
			} else {
				this.$data.current = this.$data.current - 1;
			}
		}
	}
}
